
    const click = (evt) => {

        let el = evt.target

        if (typeof evt.stopPropagation == 'function'){
            evt.stopPropagation()
        }

        if (!el._app || el._app && !el._app.click){
            el = el.parentNode
            evt._parentNode = el
        }

        if (el._app && el._app.click && el._app.click.exp){
            new Evaluate(el._app.click.exp).value(evt)
        }

    }

    module.exports = click
