
export class ModalAuto extends HTMLElement {

  constructor() {
    super();

    this.setAttribute('anim-enter','fade-in')
    this.setAttribute('anim-exit','fade-out')
    this.classList.add('exit-view')
    this.setAttribute('app-click','view.modal = false')

    if (!this.dataset.title){
        this.dataset.title = ''
    }

    this.innerHTML = `

        <section anim="{'enter':'move-up','exit':'move-down'}">

            <header>
                <div>`+this.dataset.title+`</div>
                <div class="text-right" app-click="view.modal = false">
                    <span class="icon close"></span>
                </div>
            </header>

            <section class="body">

                `+this.innerHTML+`

            </section>

        </section>

  `;
  }
}

customElements.define('modal-auto', ModalAuto);
