
import Evaluate from '../scripts/partials/Evaluate'
import http from '../scripts/partials/http'
import _get from 'lodash.get'
import View from '../scripts/partials/View'

const view = new View()

export class DropdownSearch extends HTMLElement {

    constructor() {
        super();

        this.model = this.getAttribute('app-model')

        this.innerHTML = `

            <input type="text" class="value context-link">
            <div class="dropdown context" anim="{'enter':'drop-down','exit':'drop-up'}">
                <div class="disabled text-italic text-33-grey">Type to search</div>
            </div>

        `;

        this.input = this.querySelector('input.value')
        this.dropdown = this.querySelector('.dropdown')
        this.timeout = ''

        this.addEventListener('click',this.selectDropdown)

        this.input._url = this.dataset.url
        this.input._bind = this.dataset.bind
        this.input._model = this.model
        this.input._dropdown = this.dropdown
        this.input._selectDropdown = this.selectDropdown
        this.input.addEventListener('keyup',this.search)

    }

    selectDropdown(evnt){

        let value = null,
            result = null

        if (evnt && evnt.target && evnt.target.getAttribute && evnt.target.getAttribute('value')){
            value = evnt.target.getAttribute('value')
        } else if (evnt && evnt.target && evnt.target.parentNode && evnt.target.parentNode.getAttribute && evnt.target.parentNode.getAttribute('value')){
            value = evnt.target.parentNode.getAttribute('value')
        } else if (evnt && evnt.target && evnt.target.innerHTML){
            value = evnt.target.innerHTML
        }

        if (value !== null){
            result = new Evaluate().setValue(this.model, value)
            this.input.value = evnt.target.innerHTML
        }

        if (this.dropdown.classList.contains('in-view')){

            this.dropdown.classList.remove('in-view')
            this.dropdown.classList.add('exit-view')
            this.dropdown.style.zIndex = 1

        } else {

            this.dropdown.style.display = 'block'
            this.dropdown.classList.remove('exit-view')
            this.dropdown.classList.add('in-view')
            this.dropdown.style.zIndex = 999

        }

    }

    search(evnt){

        let self = this

        if (evnt.code == 'ArrowDown'){

            let children = self._dropdown.querySelectorAll('div'),
                selected_idx = 0

            for (let i=0; i< children.length; i++){

                if (children[i].classList.contains('selected')){
                    children[i].classList.remove('selected')
                    selected_idx = i+1
                }

                if (i >= children.length-1){

                    if (selected_idx > children.length-1){
                        selected_idx = children.length-1
                    }
                    children[selected_idx].classList.add('selected')
                }

            }

        } else if (evnt.code == 'ArrowUp'){

            let children = self._dropdown.querySelectorAll('div'),
                selected_idx = 0

            for (let i=0; i< children.length; i++){

                if (children[i].classList.contains('selected')){
                    children[i].classList.remove('selected')
                    selected_idx = i-1
                }

                if (i >= children.length-1){

                    if (selected_idx < 0){
                        selected_idx = 0
                    }
                    children[selected_idx].classList.add('selected')
                }

            }

        } else if (evnt.code == 'Enter'){

            let children = self._dropdown.querySelectorAll('div'),
                selected_idx = 0

            for (let i=0; i< children.length; i++){

                if (children[i].classList.contains('selected')){

                    let value, result

                    if (children[i].getAttribute('value')){
                        value = children[i].getAttribute('value')
                    } else {
                        value = children[i].innerHTML
                    }

                    if (value !== null){
                        result = new Evaluate().setValue(self._model, value)
                        self.value = children[i].innerHTML
                    }

                    if (self._dropdown.classList.contains('in-view')){

                        // self._dropdown.classList.remove('in-view')
                        // self._dropdown.classList.add('exit-view')
                        // self._dropdown.style.zIndex = 1

                        view.exitView(self._dropdown).then(()=>{
                            self._dropdown.style.zIndex = 1
                        })

                    } else {

                        // self._dropdown.style.display = 'block'
                        // self._dropdown.classList.remove('exit-view')
                        // self._dropdown.classList.add('in-view')
                        self._dropdown.style.zIndex = 999

                        view.enterView(self._dropdown).then(()=>{

                        })

                    }


                    break;

                }

            }

        } else if (evnt.target.value && evnt.target.value.length >= 3){

            if (self._dropdown.classList.contains('exit-view')){

                self._dropdown.style.display = 'block'
                self._dropdown.classList.remove('exit-view')
                self._dropdown.classList.add('in-view')
                self._dropdown.style.zIndex = 999

            }

            http.get(self._url+evnt.target.value).then((data)=>{

                self._dropdown.innerHTML = ''

                data = JSON.parse(data)

                if (Array.isArray(data) && data.length > 0){

                    data.map((item)=>{

                        let text = _get(item, self._bind)

                        if (self._value){
                            let value = _get(item, self._value)
                            self._dropdown.innerHTML += `<div value="`+value+`">`+text+`</div>`
                        } else {
                            self._dropdown.innerHTML += `<div>`+text+`</div>`
                        }

                    })

                    if (!self._dropdown.classList.contains('in-view')){

                        // self._dropdown.style.display = 'block'
                        // self._dropdown.classList.remove('exit-view')
                        // self._dropdown.classList.add('in-view')

                        view.exitView(self._dropdown).then(()=>{

                        })

                    }

                } else {
                    self._dropdown.innerHTML = '<div class="text-italic text-33-grey">Nothing found</div>'
                }
            })

        } else {
            self._dropdown.innerHTML = '<div class="text-italic text-33-grey">Type to search</div>'
        }


    }


}

customElements.define('dropdown-search', DropdownSearch);
